}
.hero-container {
  /* background: url('/images/img-home.jpg') center center/cover no-repeat; */
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}

.hero-container > h1 {
  color: rgb(252, 249, 249);
  font-size: 100px;
  margin-top: -100px;
  text-align: center;
}

.hero-btns {
  margin-top: 32px;
}

.hero-btns .btn {
